<template>
  <div class="games page">
    <Header>
      <template #right>
        <button @click="showSearch=true" class="games__search header__button">
          <i class="games__icon-search"></i>搜游戏
        </button>
      </template>
    </Header>
    <div @scroll="scroll" ref="container" class="container">
      <banner field="game_new"></banner>
      <div ref="tab" class="games__tab">
        <ul :class="{'fixed':tabFixed}" @touchmove.prevent class="games__tab-list border-bottom">
          <li v-for="(item,index) in tabs" :key="index"
              @click="toggleTab(index)"
              :class="{'active':activeTabIndex===index}"
              class="games__tab-item">
            {{item.name}}
          </li>
        </ul>
      </div>
      <ul class="games__list clearfix">
        <li v-for="(item,index) in pageData.data" :key="index" class="games__item">
          <a class="games__img-wrapper default-bg-small" href="javascript:void (0);">
            <img class="games__img" v-lazy="wmsImgPath+item.imgUrl" :alt="item.cnName">
            <img v-if="!loginStatus&&item.canTry" class="games__icon-try"
                 :src="$useWebpImg('@/assets/img/common/icon_try.png')" alt="试玩">
            <i class="games__heart"></i>
            <p class="games__tag-wrapper">
              <span class="games__tag games__tag--provider">{{item.provider}}</span>
              <span v-if="item.isPromo" class="games__tag games__tag--promo">特惠</span>
              <span v-if="item.line" class="games__tag games__tag--line">{{item.line}}线</span>
              <span v-if="item.isPool" class="games__tag games__tag--jackpot">彩金</span>
            </p>
            <div v-if="getGameStatus(item.gameCode)" class="maintain-mask">
              <div class="maintain-mask__inner">
                <img class="maintain-mask__icon" :src="$useWebpImg('@/assets/img/common/icon_time.png')" alt="">
                <p class="maintain-mask__text">维护中</p>
              </div>
            </div>
          </a>
          <p class="games__name">{{item.cnName}}</p>
        </li>
        <li v-if="!pageData.data||!pageData.data.length" class="games__no-data-tips">很抱歉,没有找到与之相关的游戏~</li>
      </ul>
    </div>
    <searchGame v-if="showSearch" @filterGames="filterGames" :showSearch.sync="showSearch"></searchGame>
  </div>
</template>

<script>
import Header from '@/components/Header'
import Banner from '@/components/Banner.vue'
import searchGame from './searchGame.vue'
import { easeToTop } from '@/utils/util.js'
import { getGames, getPageData } from '@/utils/gameUtil.js'
import { mapGetters, mapState } from 'vuex'

export default {
  name: 'games',
  components: {
    Header,
    Banner,
    searchGame
  },
  data() {
    return {
      tabs: [
        {
          name: '全部'
        }, {
          name: '热门',
          params: { isHot: true }
        }, {
          name: '彩金池',
          params: { isPool: true }
        }, {
          name: '最新游戏',
          params: { isNew: true }
        }, {
          name: '我的收藏',
          params: { isFavorite: true }
        }
      ],
      activeTabIndex: 0,
      tabFixed: false,
      gameList: [],
      pageParams: {
        page: 1,
        pageSize: 10
      },
      filterParams: {},
      pageData: {},
      isLoadAll: false,
      showSearch: false
    }
  },
  computed: {
    ...mapState(['gameStatus']),
    ...mapGetters(['loginStatus']),
    wmsImgPath() {
      const wmsServer = window.dynamicConfig.wmsServer
      const cdnPath = window.dynamicConfig.cdnPath
      const wmsPath = window.dynamicConfig.wmsPath
      return `${wmsServer + cdnPath}/_wms/img/${wmsPath}/electronicgames/`
    },
    activeTab() {
      return this.tabs[this.activeTabIndex]
    }
  },
  activated() {
    if (!this.gameList.length) this.$loading.open('loadGames')
    this.getGames().then(() => this.$loading.hide('loadGames'))
  },
  deactivated() {
    this.tabFixed = false
    this.isLoadAll = false
    this.activeTabIndex = 0
    this.pageParams = {
      page: 1,
      pageSize: 10
    }
    this.$loading.hide('loadGames')
  },
  methods: {
    getGames() {
      const wmsServer = window.dynamicConfig.wmsServer
      const wmsPath = window.dynamicConfig.wmsPath
      const cdnPath = window.dynamicConfig.cdnPath
      const url = `${wmsServer + cdnPath}/_wms/js/${wmsPath}/electronicgames/games.js`
      return getGames(url).then(games => {
        this.gameList = games
        this.pageData = getPageData(this.gameList, this.pageParams)
      })
      // this.$axios.get(url).then(({ status, data }) => {
      //   if (status === 200 && data) {
      //     data = data.replace(/^[^[]*/, '').replace(/;$/, '')
      //     data = JSON.parse(data) || []
      //     this.gameList = data
      //   }
      // })
    },
    getPageData() {
      this.pageData = getPageData(this.gameList, {
        ...this.pageParams,
        ...this.filterParams,
        ...this.activeTab.params
      })
    },
    filterGames(params) {
      this.$refs.container.scrollIntoView()
      this.pageParams = {
        page: 1,
        pageSize: 10
      }
      this.filterParams = params
      this.getPageData()
    },
    getGameStatus(gameCode) {
      return this.gameStatus[gameCode] && this.gameStatus[gameCode].flag === 0
    },
    toggleTab(index) {
      console.log(this.$refs)
      const scrollTop = this.$refs.container.scrollTop
      this.activeTabIndex = index
      easeToTop(scrollTop, 0, 2, (val, isDone) => {
        this.$refs.container.scrollTo(0, val)
        if (isDone) {
          this.pageParams.page = 1
          this.filterParams = {}
          this.getPageData()
        }
      })
    },
    scroll(e) {
      const scrollTop = e.target.scrollTop
      const triggerHeight = e.target.scrollHeight - (scrollTop + e.target.clientHeight)
      if (this._scrollTop < scrollTop) { // 向下滚动
        if (triggerHeight < 100) this.loadNextPage()
        if (!this.tabFixed) {
          this.tabFixed = scrollTop > this.$refs.tab.offsetTop
        }
      } else { // 向上滚动
        this.tabFixed = scrollTop > this.$refs.tab.offsetTop
      }
      this._scrollTop = scrollTop
    },
    loadNextPage() {
      if (this.pageParams.page < this.pageData.maxPage) {
        this.pageParams.page++
        this.getPageData()
      } else if (!this.isLoadAll) {
        this.isLoadAll = true
        this.$toast({
          type: 'error',
          message: '已经到底了~',
          callback: () => {
            this.isLoadAll = false
          }
        })
      }
    }
  }
}
</script>

