  8: 'isPool', // 是否奖池 (0/1)
  9: 'canTry', // 是否支持试玩 (0/1)
  10: 'popularity', // 人气值
  11: 'maxMultiple', // 最大赢钱倍数
  12: 'betTimes', // 投注笔数
  13: 'maxPrize', // 最高派奖
  14: 'star', // 星级
  15: 'grade', // 分数
