<template>
  <div class="search page">
    <div class="search__header">
      <div class="search__input-wrapper">
        <label>
          <input v-model.trim="search" class="search__input" type="text" placeholder="请输入关键字...">
        </label>
      </div>
      <a @click="$emit('update:showSearch',false)" class="search__cancel" href="javascript:void (0);">取消</a>
    </div>
    <div class="search__content">
      <h6 class="search__title">游戏平台</h6>
      <ul class="search__list clearfix">
        <li v-for="(item,index) in providerList" :key="index"
            @click="selectedItem(item,'providerList')"
            :class="{'active':item.selected}" class="search__item">
          {{item.name}}
        </li>
      </ul>
      <h6 class="search__title">赔付线</h6>
      <ul class="search__list clearfix">
        <li v-for="(item,index) in lineList" :key="index"
            @click="selectedItem(item,'lineList')"
            :class="{'active':item.selected}" class="search__item">
          {{item.name}}
        </li>
      </ul>
      <ul class="search__record">
        <li v-for="(item,index) in recordList" :key="index"
            @click="search=item"
            class="search__record-item border-bottom">
          <span>{{item}}</span>
          <i @click.stop="recordList.splice(index,1)" class="search__icon-record"></i>
        </li>
      </ul>
      <button @click="submit" class="search__button button">确定</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'searchGame',
  props: {
    showSearch: Boolean
  },
  data() {
    return {
      search: null,
      lineList: [
        {
          name: '全部',
          code: null,
          selected: false
        }, {
          name: '1-4线',
          code: '1-4',
          selected: false
        }, {
          name: '5-9线',
          code: '5-9',
          selected: false
        }, {
          name: '15-25线',
          code: '15-25',
          selected: false
        }, {
          name: '30-50线',
          code: '30-50',
          selected: false
        }, {
          name: '51-243线',
          code: '51-243',
          selected: false
        }
      ],
      providerList: [
        {
          name: '全部',
          code: null,
          selected: false
        }, {
          name: 'PT',
          code: 'PT',
          selected: false
        }, {
          name: 'MG',
          code: 'MG',
          selected: false
        }, {
          name: 'AG',
          code: 'AG',
          selected: false
        }, {
          name: 'TTG',
          code: 'TTG',
          selected: false
        }, {
          name: 'PP',
          code: 'PP',
          selected: false
        }, {
          name: 'PNG',
          code: 'PNG',
          selected: false
        }
      ],
      recordList: []
    }
  },
  created() {
    const recordList = localStorage.getItem('gameSearchRecordList') || '[]'
    this.recordList = JSON.parse(recordList)
  },
  destroyed() {
  },
  methods: {
    selectedItem(item, list) {
      if (item.code === null) {
        this[list].forEach(item => {
          if (item.code !== null) item.selected = false
        })
      } else {
        this[list][0].selected = false
      }
      item.selected = !item.selected
    },
    submit() {
      let lineList = this.lineList.filter(item => item.selected).map(item => item.code)
      let providerList = this.providerList.filter(item => item.selected).map(item => item.code)
      if (this.lineList[0].selected) lineList = null
      if (this.providerList[0].selected) providerList = null
      this.$emit('update:showSearch', false)
      this.$emit('filterGames', {
        lineList,
        providerList,
        search: this.search
      })
      if (this.search && this.search !== this.recordList[0]) {
        this.recordList.unshift(this.search)
      }
      localStorage.setItem('gameSearchRecordList', JSON.stringify(this.recordList.slice(0, 3)))
    }
  }
}
</script>

